




























































































import { Component, Vue } from "vue-property-decorator";
import Queue from "../logic/queue";

@Component({})
export default class Home extends Vue {
  // public eventId: string = "127334";
  // public pointOfSaleId: string = "11647";
  public eventId: string = "";
  public pointOfSaleId: string = "";

  public shopUrl: string = "";
  public processing: boolean = false;
  public submitted: boolean = false;
  private queue: Queue = new Queue();

  // lifecycle
  public created() {
    this.queue.consoleLog = false;
  }

  // computed
  get queueNumber() {
    return this.queue.queueNumber;
  }
  get logging() {
    return this.queue.logging;
  }
  get isTest() {
    return this.queue.isTest;
  }
  set isTest(value: boolean) {
    this.queue.isTest = value;
  }

  // methods
  public start() {
    this.submitted = true;
    this.processing = true;
    this.queue
      .doRequest(this.eventId, this.pointOfSaleId)
      .then((result: string) => {
        this.shopUrl = result;
        this.processing = false;
      });
  }
}
