import jwt_decode from "jwt-decode";
import axios from "axios";

const url = "https://queue-api.paylogic.com/json/";

// testing
let retry = 0;

export default class Queue {
    public isTest: boolean = false;
    // public shopUrl: string = '';
    public queueNumber: string | number = "";
    public consoleLog: boolean = true;
    public logging: string[] = [];
    private token: string = "";

    public doRequest(eventId: string, pointOfSaleId: string): Promise<string> {
        const requestUrl = `${url}${eventId}/${pointOfSaleId}?now=${this.getTicks()}${this.token ? "&token=" + this.token : ""}`;
        this.addLogging(`requestUrl: ${requestUrl}`);

        return axios.get(requestUrl)
            .then((response: any) => {
                try {
                    const result = response.data;
                    this.addLogging(`data: ${JSON.stringify(result)}`);

                    // testing
                    if (this.isTest) {
                        if (retry < 2) {
                            retry++;
                            delete result.redirect;
                            // delete result.token;
                        }
                    }

                    if (result.redirect) {
                        // this.shopUrl = result.redirect;
                        return result.redirect;
                    } else {
                        if (result.token) {
                            let decoded: any;
                            if (this.isTest) {
                                // testing
                                decoded = { number: 12345 };
                                result.refresh_seconds = 1000;
                            } else {
                                // decode token
                                decoded = jwt_decode(result.token);
                                this.addLogging(`decoded: ${decoded}`);
                            }

                            // check queue number
                            if (decoded.number > 0) {
                                this.addLogging(`queue number: ${decoded.number}`);
                                this.queueNumber = decoded.number;
                                this.token = result.token;

                                if (result.refresh_seconds) {
                                    // refresh
                                    return new Promise((resolve, reject) => {
                                        setTimeout(() => {
                                            this.addLogging(`refreshed after ${result.refresh_seconds} sec`);
                                            resolve(this.doRequest(eventId, pointOfSaleId));
                                        }, result.refresh_seconds);
                                    });
                                } else {
                                    // refresh - maybe put timeout otherwise 503 forbidden
                                    this.addLogging("instant refreshed");
                                    return this.doRequest(eventId, pointOfSaleId);
                                }
                            } else {
                                // refresh because no queue number
                                this.addLogging("refresh because no queue number");
                                return this.doRequest(eventId, pointOfSaleId);
                            }
                        } else {
                            // refresh because no token
                            this.addLogging("refresh because no token");
                            return this.doRequest(eventId, pointOfSaleId);
                        }
                    }
                } catch (exception) {
                    // refresh because exception
                    this.addLogging(`refresh because exception: ${exception}`);
                    return this.doRequest(eventId, pointOfSaleId);
                }
            })
            .catch((exception) => {
                // http error
                this.addLogging(`http error: ${exception}`);
                return this.doRequest(eventId, pointOfSaleId);
            });
    }

    private getTicks() {
        const d = new Date();
        return d.getTime();
    }

    private addLogging(msg: string) {
        if (this.consoleLog) {
            console.log(msg);
        }
        this.logging.unshift(msg);
    }

}
